.continer {
  background-color: rgb(95, 90, 90);
  padding-inline: 2%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.title {
  font-size: 25px;
  color: #000;
  font-family: "Bungee Inline";
  /* text-align: center; */
  /* background-color: red; */
  width: fit-content;
  cursor: pointer;
  animation-name: example;
  animation-duration: 1s;
  animation-iteration-count: 1;
  transition-duration: 2s;
}
.headerScreen h2 {
  margin: 0%;
  padding: 0%;
  font-size: 20px;
  /* color: burlywood; */
}
@keyframes example {
  0% {
    color: rgb(255, 255, 0);
    /* transform: translateX(5px); */
  }
  50% {
    color: red;
    /* transform: translateX(-5px); */
  }
}
.headerScreen {
  display: flex;
  align-items: center;
}
.headerScreen h1 {
  font-size: 18px;
  margin-inline: 20px;
  cursor: pointer;
  transition-duration: 0.3s;
}
.headerScreen h1:hover {
  color: #1f1745;
  transform: translateX(5px);

  /* position: absolute; */
  /* margin-top: -2px; */
}
